/**
 * @jest-environment jsdom
 */
import { renderBridgeHook } from './test-utils/index';
import { useTryonWorkflow } from '../src/hooks/useTryonWorkflow';
import { useSingleImageUpload } from '../src/hooks/useImageUpload';
import { useToast } from '../src/hooks/index';

// Mock business layer dependencies
jest.mock('../src/business-layer', () => ({
  useTryonMutation: jest.fn(() => ({
    mutate: jest.fn(),
    mutateAsync: jest.fn(),
    data: undefined,
    error: null,
    isError: false,
    isSuccess: false,
    isLoading: false,
    reset: jest.fn(),
  })),
  useImageProcessing: jest.fn(() => ({
    processImage: jest.fn(),
    processBasic: jest.fn(),
    isProcessing: false,
    error: null,
  })),
  useImageValidation: jest.fn(() => ({
    data: { isValid: true, errors: [] },
    error: null,
    isLoading: false,
    isError: false,
  })),
  useImageMetadata: jest.fn(() => ({
    data: { dimensions: { width: 1024, height: 768 }, format: 'image/jpeg', size: 102400 },
    error: null,
    isLoading: false,
    isError: false,
  })),
  useImageThumbnail: jest.fn(() => ({
    data: 'data:image/jpeg;base64,mock-thumbnail',
    error: null,
    isLoading: false,
    isError: false,
  })),
  processImageForTryon: jest.fn(),
  resizeImageTo1024x1536: jest.fn(),
}));

describe('Debug Bridge Layer Dependencies', () => {
  it('should render useToast without error', () => {
    const { result } = renderBridgeHook(() => {
      const { showToast } = useToast();
      return { showToast };
    });

    expect(result.current).not.toBeNull();
    expect(typeof result.current.showToast).toBe('function');
  });

  it('should render useTryonWorkflow without error', () => {
    const { result } = renderBridgeHook(() => {
      const workflow = useTryonWorkflow();
      return { workflow };
    });

    expect(result.current).not.toBeNull();
    expect(result.current.workflow).toBeDefined();
  });

  it('should render useSingleImageUpload without error', () => {
    const { result } = renderBridgeHook(() => {
      const upload = useSingleImageUpload();
      return { upload };
    });

    expect(result.current).not.toBeNull();
    expect(result.current.upload).toBeDefined();
  });

  it('should render all dependencies together', () => {
    const { result } = renderBridgeHook(() => {
      const { showToast } = useToast();
      const workflow = useTryonWorkflow({}, showToast);
      const userUpload = useSingleImageUpload();
      const apparelUpload = useSingleImageUpload();
      
      return { showToast, workflow, userUpload, apparelUpload };
    });

    expect(result.current).not.toBeNull();
    expect(result.current.showToast).toBeDefined();
    expect(result.current.workflow).toBeDefined();
    expect(result.current.userUpload).toBeDefined();
    expect(result.current.apparelUpload).toBeDefined();
  });
});